.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

/* CSS */
button {
  background: #f03b16;
  border: 1px solid #f03b16;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.1) 1px 2px 4px;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  display: inline-block;
  font-family: nunito,roboto,proxima-nova,"proxima nova",sans-serif;
  font-size: 16px;
  font-weight: 800;
  line-height: 16px;
  min-height: 40px;
  outline: 0;
  padding: 12px 14px;
  text-align: center;
  text-rendering: geometricprecision;
  text-transform: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
  position: relative;
  top: 10px;
  left: 5px;
  margin-bottom: 15px;
}

button:hover,
button:active {
  background-color: initial;
  background-position: 0 0;
  color: #f03b16;
}

button:active {
  opacity: .5;
}


.reset {
  position: absolute;
  top: 2px;
  right: 10px;
}

.somebox {
  position: relative;
  left: 5px
}

.plump {
  width: 20px;
  position: relative;
  left: 4px;
  border-left-width: 4px;
}

.specs {
  padding-bottom: 0px;
  margin-bottom: 0px;
}

.specbox {
  margin-bottom: 20px;
}

.leaderboard {
  border: 2px solid;
  border-collapse: collapse;
}

.playerbox {
  border: solid;
  border-color: black;
  margin-bottom: 5px;
  margin-top: 5px;
  position: relative;
  left: 5px;
  width: 200px;
}

.playerboxfailed {
  border: solid;
  border-color: black;
  margin-bottom: 5px;
  margin-top: 5px;
  position: relative;
  left: 5px;
  width: 200px;
  background-color: red;
}



table {
  position: relative;
  left: 5px;
}

table td {
  border: 1px solid black;
}

table th {
  border: 1px solid black;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
